:root {
  --warna-biru: hsl(213, 76%, 46%);
  --warna-cream: hsl(28, 76%, 97%);
  --warna-merah: hsl(343, 81%, 53%);
  --warna-pink: hsl(353, 95%, 93%);
  --warna-pink-tua: hsl(352, 27%, 33%);
  --warna-kuning: hsl(46, 92%, 67%);
  --warna-kuning-tua: #b69c43;
  --warna-coklat: hsl(26, 57%, 89%);
  --warna-coklat-tua: #6b513d;
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1,
h2,
h3,
p {
  margin: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow: hidden;
  position: fixed;
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}